.col-sm {
  float: left;
  height: 100vh;
  width: 202px;
  padding-top: 80px;
}

.col-md {
  float: left;
  height: 100vh;
  width: 30%;
  padding-top: 80px;
}

.col-lg {
  float: left;
  height: 100vh;
  width: calc(70% - 202px);
  padding-top: 80px;
  background-color: burlywood;
}

header {
  width: 100%;
}

.main-nav {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #ffffff;
  padding: 20px 22px 20px 26px;
}
.main-nav .col {
  float: left;
  width: 33.33%;
}
.main-nav .brand {
  float: left;
}
.main-nav .brand .brand-img {
  display: inline-block;
  width: 15px;
  height: 20px;
  border-radius: 0 0 20px 20px;
  background-color: #000000;
}
.main-nav .brand .brand-text {
  font-size: 20px;
  color: #000000;
}
.main-nav .notification {
  display: inline-block;
  font-size: 16px;
  color: #8e8e93;
  padding-top: 5px;
}
.main-nav .search-bar {
  float: right;
}

.side-nav {
  position: relative;
}
.side-nav .button-container {
  position: absolute;
  bottom: 0;
  width: 100%;
}
.side-nav .button-container button {
  width: 50%;
  height: 60px;
  border-radius: 0;
  float: left;
  color: #6f6f75;
}
.side-nav .button-container button:first-child {
  border-right: 1px solid #ededed;
}
.side-nav .button-container button:hover {
  background-color: rgba(142, 142, 147, 0.188);
}

.mail-nav article {
  position: relative;
  padding: 22px 16px 22px 20px;
  border-bottom: 1px solid #ededed;
  height: 90px;
  cursor: pointer;
}
.mail-nav article:first-child {
  padding-top: 0px;
}
.mail-nav article .heading {
  float: left;
  font-size: 15px;
  color: #4c4f59;
}
.mail-nav article .time {
  float: right;
  font-size: 12px;
  color: #4c4f59;
}
.mail-nav article .title {
  font-size: 12px;
  color: #a3a3a8;
  float: left;
}
.mail-nav article.active::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 4px;
  background-color: #007aff;
}

.icon-nav {
  float: right;
}
.icon-nav li {
  float: right;
  margin-left: 8px;
}
.icon-nav li button i {
  color: rgba(31, 34, 46, 0.5);
  font-size: 12px;
}

.search-bar {
  width: 294px;
  height: 30px;
  border-radius: 0;
  position: relative;
}
.search-bar button {
  height: 100%;
  position: absolute;
  border: none;
  background-color: transparent;
  top: 50%;
  transform: translateY(-50%);
  padding: 0;
}
.search-bar .icon-btn {
  left: 0;
}
.search-bar .icon-btn .fa-magnifying-glass {
  color: #b4b0af;
  font-size: 12px;
  margin-left: 6px;
}
.search-bar .dropdown-btn {
  left: 25px;
}
.search-bar .dropdown-btn .fa-chevron-down {
  font-size: 8px;
  color: #b4b0af;
}
.search-bar input {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.07);
  padding: 0 20px 0 40px;
  border: none;
}
.search-bar input:focus {
  outline: none;
}

.button-primary {
  width: 164px;
  height: 36px;
  border-radius: 2px;
  background-color: #007aff;
  color: #ffffff;
  border: none;
  font-family: "opensans";
  font-size: 12px;
  font-weight: normal;
  font-style: normal;
  text-transform: uppercase;
  transition: background-color 300ms ease;
  padding: 0;
}
.button-primary:hover {
  background-color: #005dc2;
}

.button-group li {
  position: relative;
}
.button-group li button {
  width: 164px;
  height: 36px;
  border-radius: 0;
  background-color: transparent;
  color: #8e8e93;
  border: none;
  font-family: "opensans";
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  text-transform: none;
  transition: background-color 300ms ease;
  text-align: left;
}
.button-group li button:hover {
  background-color: rgba(0, 0, 0, 0);
}
.button-group li button i {
  margin-right: 15px;
}
.button-group li button [class|=icon] {
  margin-right: 15px;
}
.button-group li button.active {
  color: #4c4f59;
}
.button-group li .dropdown {
  position: relative;
}
.button-group li .dropdown i {
  width: 12px;
  height: 12px;
  border-radius: 2px;
  position: absolute;
  font-size: 5px;
  top: 50%;
  transform: translateY(-50%);
}
.button-group li .dropdown i::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.button-group li:hover {
  background-color: rgba(142, 142, 147, 0.188);
}
.button-group li:hover button {
  color: #4c4f59;
}
.button-group li:hover .dropdown i {
  background-color: #70727a;
  color: #ffffff;
  border: none;
}

.icon-quinary, .icon-quatenary, .icon-tertiary, .icon-secondary, .icon-primary {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 1px;
}

.icon-primary {
  border: 3px solid #9200ff;
}

.icon-secondary {
  border: 3px solid #ff7100;
}

.icon-tertiary {
  border: 3px solid #d0db00;
}

.icon-quatenary {
  border: 3px solid #00dcff;
}

.icon-quinary {
  border: 3px solid #6f6e74;
}